.category-list__item {
  background-color: $gray;
  padding: 25px 20px 22px;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.category-list__wrap-img {
  position: relative;
  width: 50px;
  height: 50px;
  z-index: 0;
  background-color: $green;
  border-radius: 50%;
  margin-left: 2px;
  margin-right: 19px;
}

.category-list__img {
  position: absolute;
  top: -4px;
  left: -50%;
  right: -50%;
  margin: auto;
}

.category-list__title {
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  font-family: "Oswald", "Arial", sans-serif;
  color: $black111;
  margin-top: 4px;
  margin-bottom: 29px;
  text-transform: uppercase;
}

.category-list__desc {
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 43px;
  width: 100%;
}

.category-list__link {
  color: $black111;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  font-family: "Oswald", "Arial", sans-serif;
  position: relative;
  padding-right: 48px;

  &:active {
    opacity: 0.3;
  }

  &:active .category-list__link-arrow,
  &:hover .category-list__link-arrow {
    display: none;
  }

  &:active .category-list__link-arrow-verylong,
  &:hover .category-list__link-arrow-verylong {
    display: block;
    right: 3px;
  }
}

.category-list__link-arrow-verylong,
.category-list__link-arrow {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 11px;
  margin: auto;
}

.category-list__link-arrow-verylong {
  display: none;
}

@media (max-width: 767px) {
  .category-list__desc {
    position: relative;

    &::after {
      background-color: $gray-200;
      content: "";
      position: absolute;
      bottom: 19px;
      left: 0;
      right: 0;
      height: 1px;
    }
  }
}

@media (min-width: 768px) {
  .category-list__item {
    padding: 77px 280px 61px 52px;
    margin-bottom: 30px;
    position: relative;

    &:nth-child(1) .category-list__img {
      height: 195px;
      width: auto;
      margin-top: -15px;
    }

    &:nth-child(2) .category-list__img {
      height: 230px;
      width: auto;
    }
  }

  .category-list__title {
    font-size: 36px;
    line-height: 37px;
    margin-bottom: 24px;
    margin-top: 0;
  }

  .category-list__desc {
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 57px;
    min-height: 96px;
  }

  .category-list__wrap-img {
    height: 200px;
    margin-left: 0;
    margin-right: 0;
    margin-top: 16px;
    width: 200px;
    position: absolute;
    top: 77px;
    right: 62px;

    &::before {
      bottom: 0;
      left: 50%;
      width: 200px;
      height: 200px;
      transform: translateX(-46%);
    }
  }

  .category-list__img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .category-list__link {
    font-size: 20px;
    line-height: 20px;
    padding-right: 60px;
  }

  .category-list__link-arrow {
    right: 12px;
  }
}

@media (min-width: 1300px) {
  .category-list {
    display: flex;
    justify-content: space-between;
  }

  .category-list__item {
    width: 462px;
    margin-bottom: 0;
    padding-top: 46px;
    padding-right: 56px;

    &:nth-child(1) .category-list__img {
      height: 98px;
      margin-top: -7px;
    }

    &:nth-child(2) .category-list__img {
      height: 115px;
    }
  }

  .category-list__wrap-img {
    margin-top: 0;
    margin-right: 62px;
    position: relative;
    top: auto;
    right: auto;
    height: 100px;
    width: 100px;
  }

  .category-list__title {
    width: auto;
    margin-top: 30px;
    margin-bottom: 75px;
  }

  .category-list__desc {
    width: 100%;
    padding-bottom: 6px;
  }
}
