/* oswald regular */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src:
    local("Oswald"),
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}

/* oswald medium */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src:
    local("Oswald"),
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
